var ghPagesInput2 = 'so i just completed a todo app the other day using `React`. this was the second time i followed the course “build your first production quality react application” on [egghead.io](https://egghead.io/). the first time i followed the course exactly, using `create-react-app`. however, i kept on encountering errors in the console. probably some of them were due to errors on my part, but some definitely related to create-react-app. and the thing is, i wasn’t able to figure out what the issue was without running the eject command to see what was under the hood. and if i had done that, there was no going back! well, that just didn’t sit well with me. i decided to learn more about `Webpack 2+` so that i could start using the new features it had to offer to create my own `React` workflow, thereby only adding features that i needed. i love creating my own automation processes for my workflows. i have worked hard at creating seamless processes for regular `ES6+` `JavaScript` applications using `Gulp`, `templating engines`, and `webpack`. call me nerdy, but i find it a lot of fun and extremely useful! i am yet to take a deep dive into a more complex workflow for `Hugo` (this site is built on it), but that will eventually happen as well.';

